import React from "react";
import {
  Card as Mcard,
  makeStyles,
  Typography,
  useTheme,
} from "@material-ui/core";
import { Computer, Code, Web, Https } from "@material-ui/icons";

function SkillCard({ title, state, children, ...props }) {
  const classes = useStyle();
  const theme = useTheme();

  let icon;
  switch (title) {
    case "Langages":
      icon = <Code color="primary" />;
      break;
    case "Backend":
      icon = <Https color="primary" />;
      break;
    case "Frontend":
      icon = <Web color="primary" />;
      break;
    case "Outils":
      icon = <Computer color="primary" />;
      break;
    default:
      icon = null;
      break;
  }
  const isTrue = state === title;

  return (
    <Mcard
      {...props}
      className={classes.root}
      style={{ background: isTrue && theme.gradientColor }}
    >
      <div>
        <Typography variant="subtitle1" style={{ fontWeight: 600 }}>
          {title}
        </Typography>
        <Typography variant="subtitle2" color="textSecondary">
          {children}
        </Typography>
      </div>
      <span className={classes.icon}>{icon}</span>
    </Mcard>
  );
}

const useStyle = makeStyles((theme) => {
  return {
    root: {
      width: 175,
      height: 175,
      cursor: "pointer",
      borderStyle: "none",
      transition: "transform 0.3s ease",
      boxShadow: `2px 2px 12px ${
        theme.palette.type === "light" ? "rgba(0,0,0,0.1)" : "rgba(0,0,0,0.2)"
      }`,
      "&:hover": {
        transform: "translateY(8px)",
      },
      [theme.breakpoints.down("xs")]: {
        minWidth: "90%",
      },
      display: "flex",
      position: "relative",
      alignItems: "center",
      gap: theme.spacing(2),
      padding: theme.spacing(1),
    },
    icon: {
      position: "absolute",
      top: 8,
      right: 8,
      padding: 4,
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      borderRadius: "50%",
    },
  };
});

export default SkillCard;
