import { blue, lightBlue } from "@material-ui/core/colors";

const darkGradient = "linear-gradient(225deg,#141e30,#141e30,#243b55aa)";
const lightGradient =
  "linear-gradient(225deg,#ffffffaa,#ffffffaa, #F3F6F9, #F3F6F9)";

export const light = {
  switch: darkGradient,
  gradientColor: lightGradient,
  imageColor: "#141e30",
  footer: "linear-gradient(180deg,#efefff 0%, #efefff 100%)",
  boxShadow: "inset 0px -1px 1px #eaeeff",
  palette: {
    primary: blue,
    secondary: lightBlue,
    background: {
      default: "rgba(255,255,255,0.72)",
    },
    type: "light",
  },
  typography: {
    fontFamily: ["JosefinSans-Regular"].join(","),
  },
};

export const dark = {
  switch: lightGradient,
  gradientColor: darkGradient,
  borderColor: "#243b55",
  imageColor: "#ffff",
  footer: "linear-gradient( #243b55aa 0%, #243b55aa 100%)",
  boxShadow: "inset 0px -1px 1px rgba(255, 255, 255, 0.12)",
  palette: {
    primary: blue,
    secondary: lightBlue,
    background: {
      default: "#141e3099",
      paper: "#141e30",
    },
    type: "dark",
  },
  typography: {
    fontFamily: ["JosefinSans-Regular"].join(","),
  },
};
