import React from "react";
import { Snackbar as MuiSnackBar } from "@material-ui/core";
import { Alert } from "@material-ui/lab";

function SnackBar({ copied, failed, open, setOpen }) {
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setOpen(false);
  };

  return (
    <MuiSnackBar
      anchorOrigin={{ vertical: "bottom", horizontal: "left" }}
      open={open}
      autoHideDuration={3000}
      onClose={handleClose}
    >
      <Alert severity={(copied && "success") || (failed && "error")}>
        {copied ? "Copier avec succès" : ""}
        {failed ? "Erreur de copie" : ""}
      </Alert>
    </MuiSnackBar>
  );
}

export default SnackBar;
