import React, { useState } from "react";
import { Button, ButtonGroup } from "@material-ui/core";
import { useDispatch } from "react-redux";
import { darkMode, lightMode } from "../../redux/actions/actionTheme";

function Switch() {
  const [switchOn, setSwitchOn] = useState(false);
  const dispatch = useDispatch();

  const switchTheme = () => {
    setSwitchOn(!switchOn);
    if (switchOn) dispatch(lightMode());
    else dispatch(darkMode());
  };

  const [isVerySmallScreen] = useState(document.body.offsetWidth <= 330);

  return (
    <ButtonGroup
      variant="text"
      orientation={isVerySmallScreen ? "vertical" : "horizontal"}
    >
      <Button
        key="light"
        startIcon={"🌞"}
        style={{ textTransform: "inherit" }}
        onClick={switchTheme}
        disabled={!switchOn}
      >
        Mode éclairé
      </Button>
      <Button
        key="dark"
        startIcon={"🌜"}
        style={{ textTransform: "inherit" }}
        onClick={switchTheme}
        disabled={switchOn}
      >
        Mode sombre
      </Button>
    </ButtonGroup>
  );
}

export default Switch;
