import React from "react";
import { Chip, makeStyles, Typography, Paper } from "@material-ui/core";
import { OpenInNew } from "@material-ui/icons";

function ProjectCard({ data, ...props }) {
  const classes = useStyle();
  const { poster, badge, date, title, technos, link } = data;

  return (
    <Paper key={props.key} className={classes.container} variant="outlined">
      <span className={classes.badge}>{badge}</span>
      <div className={classes.imgWrapper} onClick={props.onClick}>
        <img className={classes.img} src={poster} alt="project dashboard" />
        <div className="slide">
          <Typography variant="subtitle2">{date}</Typography>
        </div>
      </div>
      <div className={classes.infoWrapper}>
        <a
          className="link"
          href={link}
          style={{ display: "flex", gap: 2, alignItems: "center" }}
        >
          {title}
          <OpenInNew color="primary" style={{ fontSize: 14 }} />
        </a>
        <div>
          {Boolean(technos[0]) &&
            technos.map((techno, index) => (
              <Chip
                key={index}
                label={techno}
                size="small"
                variant="outlined"
              />
            ))}
        </div>
      </div>
    </Paper>
  );
}

const useStyle = makeStyles((theme) => {
  return {
    container: {
      width: 275,
      position: "relative",
      cursor: "pointer",
      padding: theme.spacing(1),
      "& .link": {
        color: theme.palette.text.primary,
        fontSize: 16,
      },
      [theme.breakpoints.down(350)]: {
        width: 250,
      },
    },
    badge: {
      background: theme.palette.primary.dark,
      position: "absolute",
      top: 14,
      left: -14,
      zIndex: 2,
      padding: "0 4px",
      borderRadius: 4,
      fontSize: "10px",
      color: "white",
      fontWeight: 600,
      whiteSpace: 2,
      textTransform: "capitalize",
    },
    imgWrapper: {
      overflow: "hidden",
      height: 140,
      "& .slide": {
        transform: "translateY(0px)",
        transition: "transform 0.5s",
        background: theme.gradientColor,
        height: "31px",
        padding: 4,
      },
      "&:hover img": {
        transform: "scale(1)",
      },
      "&:hover .slide": {
        transform: "translateY(-53px)",
        [theme.breakpoints.down(350)]: {
          transform: "translateY(-38px)",
        },
      },
    },
    img: {
      width: 275,
      objectFit: "scale-down",
      transition: "transform 0.3s",
      transform: "scale(1.1)",
      [theme.breakpoints.down(350)]: {
        width: 250,
      },
    },
    infoWrapper: {
      padding: "8px 0 0 0",
      "& div": {
        display: "flex",
        gap: 4,
      },
    },
  };
});

export default ProjectCard;
