import React from "react";
import { Button } from "@material-ui/core";

function SkillButton({ title, state, ...props }) {
  const isTrue = title === state;
  return (
    <Button
      {...props}
      disabled={title.includes("Prix")}
      disableElevation
      color={isTrue ? "primary" : "default"}
    >
      {title}
    </Button>
  );
}

export default SkillButton;
