import React, { useEffect, useRef, useState } from "react";
import { makeStyles, Button } from "@material-ui/core";
import { Done, Close } from "@material-ui/icons";
import CopyIcon from "../CopyIcon";
import SnackBar from "./SnackBar";

function PressPapier(props) {
  const classes = useStyles();
  const inputRef = useRef();
  const [copied, setCopied] = useState(false);
  const [failed, setFailed] = useState(false);
  const [open, setOpen] = useState(false);

  function updateClipboard(newClip) {
    if (navigator.clipboard === undefined) {
      setFailed(true);
      setOpen(true);
    } else {
      navigator.clipboard.writeText(newClip).then(
        function () {
          setCopied(true);
          setOpen(true);
        },
        function () {
          setFailed(true);
          setOpen(true);
        }
      );
    }
  }

  useEffect(() => {
    inputRef.current.onclick = function () {
      updateClipboard(props.content);
    };
    const intervale = setInterval(() => {
      copied && setCopied(false);
      failed && setFailed(false);
    }, 3000);
    return () => {
      clearInterval(intervale);
    };
  });

  return (
    <>
      <Button
        ref={inputRef}
        variant="outlined"
        className={classes.root}
        startIcon={
          failed ? (
            <Close />
          ) : copied ? (
            <Done />
          ) : (
            <CopyIcon style={{ padding: 3 }} />
          )
        }
      >
        {props.content}
      </Button>
      <SnackBar open={open} setOpen={setOpen} copied={copied} failed={failed} />
    </>
  );
}

const useStyles = makeStyles((theme) => ({
  root: {
    textTransform: "lowerCase",
  },
}));

export default PressPapier;
