export const DARK_MODE = "DARK_MODE";
export const LIGHT_MODE = "LIGHT_MODE";

export const darkMode = () => {
    return (dispatch) => {
        dispatch({ type: DARK_MODE, payload: "dark" });
    };
};

export const lightMode = () => {
    return (dispatch) => {
        dispatch({ type: LIGHT_MODE, payload: "light" });
    };
};
