import React from "react";
import { Box as Mbox, makeStyles, Typography } from "@material-ui/core";

function Box(props) {
  const { children, title, className } = props;
  const classes = useStyle();
  return (
    <Mbox {...props} className={[classes.root, className]}>
      <Typography className={classes.title}>{title}</Typography>
      {children}
    </Mbox>
  );
}

const useStyle = makeStyles((theme) => {
  return {
    root: {
      position: "relative",
      paddingLeft: 30,
      paddingRight: 25,
      minHeight: 250,
      minWidth: 250,
    },
    title: {
      fontSize: 14,
      position: "absolute",
      transform: "rotate(90deg)",
      transformOrigin: "top left",
      top: 0,
      paddingTop: 5,
      paddingBottom: 5,
      "&::after": {
        content: "''",
        width: theme.spacing(15),
        height: theme.spacing(0.5),
        background: theme.gradientColor,
        position: "absolute",
        top: "45%",
        transform: "translateX(10%)",
      },
    },
  };
});

export default Box;
