import React from "react";
import { makeStyles, Link } from "@material-ui/core";
import { Facebook, Twitter, GitHub } from "@material-ui/icons";
import { motion } from "framer-motion";

const container = {
  show: {
    transition: {
      staggerChildren: 0.2,
      delayChildren: 0.8,
    },
  },
};

const item = {
  hidden: { opacity: 0, y: 20 },
  show: {
    opacity: 1,
    y: 0,
    transition: {
      ease: [0.6, 0.01, -0.05, 0.9],
      duration: 0.4,
    },
  },
};

function Social() {
  const classes = useStyles();
  return (
    <motion.div
      className={classes.wrapper}
      variants={container}
      initial="hidden"
      animate="show"
    >
      <motion.div className={classes.social} variants={item}>
        <Facebook className={classes.root} />
        <Link
          href="https://facebook.com/ks.krimi"
          target="_blank"
          className={classes.link}
        >
          Facebook
        </Link>
      </motion.div>
      <motion.div className={classes.social} variants={item}>
        <Twitter className={classes.root} />
        <Link
          href="https://twitter.com/ks-krimi"
          target="_blank"
          className={classes.link}
        >
          Twitter
        </Link>
      </motion.div>
      <motion.div className={classes.social} variants={item}>
        <GitHub className={classes.root} />
        <Link
          href="https://github.com/ks-krimi"
          target="_blank"
          className={classes.link}
        >
          Github
        </Link>
      </motion.div>
    </motion.div>
  );
}

const useStyles = makeStyles((theme) => {
  return {
    root: {
      width: "0.8em",
      height: "0.8em",
      padding: 2,
    },
    link: {
      fontSize: 8,
      fontWeight: 600,
      textTransform: "uppercase",
      color: "inherit",
    },
    wrapper: {
      display: "flex",
      gap: theme.spacing(2),
    },
    social: {
      gap: theme.spacing(0.5),
      display: "flex",
      marginTop: theme.spacing(2),
      height: "1em",
    },
  };
});

export default Social;
