import React from "react";
import { motion } from "framer-motion";
import { makeStyles } from "@material-ui/core";
import TechnoWrapper from "../TechnoWrapper";

const container = {
  show: {
    transition: {
      staggerChildren: 0.35,
      delay: 0.8,
    },
  },
};

const itemMain = {
  hidden: { opacity: 0, y: 200 },
  show: {
    opacity: 1,
    y: 0,
    transition: {
      delay: 0.35,
      ease: [0.6, 0.01, -0.05, 0.95],
      duration: 1.6,
    },
  },
};

function PreLoader({ setShow }) {
  const classes = useStyle();

  return (
    <motion.div className={classes.container}>
      <motion.div
        style={{ position: "relative" }}
        variants={container}
        onAnimationComplete={() => setShow(false)}
        initial="hidden"
        animate="show"
      >
        <motion.div
          variants={itemMain}
          layoutId="profile-image-container"
          className={classes.imgWrapper}
        >
          <motion.img
            layoutId="profile-image"
            src={process.env.PUBLIC_URL + `/img/profile.png`}
            alt="profile"
          />
        </motion.div>
        <TechnoWrapper />
      </motion.div>
    </motion.div>
  );
}

const useStyle = makeStyles((theme) => {
  return {
    container: {
      position: "fixed",
      transformOrigin: "center",
      width: "100vw",
      height: "100vh",
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
    },
    imgWrapper: {
      maxWidth: 320,
      height: "auto",
      background: theme.gradientColor,
      "& img": {
        width: "100%",
        height: "auto",
        objectFit: "cover",
      },
    },
  };
});

export default PreLoader;
