import React from "react";
import { makeStyles } from "@material-ui/core";
import { motion } from "framer-motion";

const variants = {
  hidden: {
    y: -48,
    opacity: 0,
    transition: {
      duration: 0.35,
      ease: "easeOut",
    },
  },
  show: {
    y: 0,
    opacity: 1,
    transition: {
      duration: 0.35,
      ease: "easeOut",
    },
  },
};

function LoadingBar({ name, value }) {
  const classes = useStyle();
  return (
    <motion.div variants={variants} className={classes.container}>
      <div className="left">{name}</div>
      <div className="right">{value}%</div>
      <div className="bar">
        <div className="value" style={{ width: `${value}%` }}></div>
      </div>
    </motion.div>
  );
}

const useStyle = makeStyles((theme) => {
  return {
    container: {
      maxWidth: 400,
      minHeight: 24,
      position: "relative",
      display: "grid",
      gridTemplateColumns: "1fr 1fr",
      gridTemplateRows: "1fr 12px",
      gridTemplateAreas: '"left right""bar bar"',
      margin: "8px 0",
      "& .left": {
        gridArea: "left",
      },
      "& .right": {
        gridArea: "right",
        textAlign: "end",
      },
      "& .bar": {
        gridArea: "bar",
        height: 14,
        borderRadius: 2,
        padding: 2,
        boxShadow: `0 0 2px ${
          theme.palette.type === "light"
            ? "rgba(0,0,0,0.3)"
            : "rgba(255,255,255,0.3)"
        }`,
        display: "flex",
        alignItems: "center",
        "& .value": {
          height: "8px",
          background: theme.palette.primary.main,
          position: "relative",
          overflow: "hidden",
          borderTopLeftRadius: 2,
          borderBottomLeftRadius: 2,
          "&::after": {
            content: "''",
            background: theme.palette.background.paper,
            width: 20,
            height: 16,
            borderRadius: "35%",
            position: "absolute",
            top: "50%",
            left: "calc(100% + 7px)",
            animation: "$rotate 5s linear infinite",
          },
        },
      },
    },
    "@keyframes rotate": {
      "0%": {
        transform: "translate(-50%, -50%) rotate(0deg)",
      },

      "50%": {
        transform: "translate(-50%, -50%) rotate(180deg)",
      },
      "100%": {
        transform: "translate(-50%, -50%) rotate(360deg)",
      },
    },
  };
});

export default LoadingBar;
