import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core";
import { Close } from "@material-ui/icons";
import { motion } from "framer-motion";
import Switch from "../Switch";
import Navigation from "../Navigation";

function Menu() {
  const classes = useStyle();
  const [show, setShow] = useState(false);

  const open = () => {
    setShow(true);
  };

  const close = () => {
    setShow(false);
  };

  useEffect(() => {
    window.addEventListener("keydown", (e) => {
      if (e.key === "Escape" && show) {
        setShow(false);
      }
    });
  }, [show]);

  return (
    <div
      className={classes.container}
      style={{
        zIndex: show && 10,
        backgroundColor: show && "rgba(24, 24, 27, 0.8)",
      }}
    >
      {show && <div className="closeLayer" onClick={close}></div>}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.2 }}
        className="menu"
        style={{ transform: show && "translate(-10px, -50%)" }}
        onClick={open}
      />
      <motion.div
        className="navigation"
        style={{ transform: show && "translate(0px,0%)" }}
      >
        <div onClick={close} className={classes.closeBtn}>
          <Close fontSize="large" color="action" />
        </div>
        <div className="header">Menu</div>
        <div className="outils">
          <Navigation onClick={close} />
        </div>
        <div className="header">Mode</div>
        <div className="switch">
          <Switch />
        </div>
      </motion.div>
    </div>
  );
}

const useStyle = makeStyles((theme) => ({
  container: {
    position: "fixed",
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    pointerEvents: "none",
    zIndex: 1,
    "& .closeLayer": {
      height: "100vh",
      width: "100vw",
      backdropFilter: "blur(2px)",
      transition: "0.3s",
      pointerEvents: "all",
    },
    "& .menu": {
      backgroundColor: theme.palette.primary.main,
      width: 9,
      height: 100,
      borderTopRightRadius: 8,
      borderBottomRightRadius: 8,
      cursor: "pointer",
      position: "absolute",
      transform: "translate(-2px, -50%)",
      top: "50%",
      transition: "0.3s ease",
      transitionDelay: "0.3s",
      pointerEvents: "all",
      "&:hover": {
        transform: "translate(0px, -50%)",
        backgroundColor: theme.palette.primary.dark,
      },
    },
    "& .navigation": {
      position: "absolute",
      display: "flex",
      flexDirection: "column",
      top: 0,
      bottom: 0,
      width: 450,
      transform: "translate(-450px, 0%)",
      padding: 50,
      transition: "0.2s ease-out",
      zIndex: 11,
      pointerEvents: "all",
      borderRadius: 0,
      overflowY: "auto",
      background: theme.palette.background.paper,
      [theme.breakpoints.only("xs")]: {
        width: "100%",
        transform: "translate(-100%, 0%)",
      },
      "& .header": {
        padding: "24px 0",
        fontSize: "1.2rem",
      },
      "& .outils": {
        height: "100%",
        padding: 4,
      },
      "& .switch": {
        justifyContent: "center",
        display: "flex",
      },
    },
  },
  closeBtn: {
    position: "absolute",
    right: 0,
    top: 0,
    height: 50,
    width: 50,
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    cursor: "pointer",
  },
}));

export default Menu;
